.enemy-container {
  min-width: 300px;
  min-height: 380px;
  overflow: hidden;
  display: flex;
  justify-content: center;
}

.enemy {
  width: 512px;
  height: 512px;
  background-position: -5px -5px;
  background-repeat: no-repeat;
  display: block;
  margin: 0 auto;
  transform: scale(-1, 1);
}

.enemy.stand {
  background-image: url(./img/IceWizard/Stand/sprite.png);
  animation: enemy-stand 1s steps(1) infinite;
}

.enemy.die {
  background-image: url(./img/IceWizard/Die/sprite.png);
  animation: enemy-die 1s steps(1) 1 forwards;
}

.enemy.hit {
  background-image: url(./img/IceWizard/Hit/sprite.png);
  animation: enemy-hit 1s steps(1) 1;
}

.enemy.attack {
  background-image: url(./img/IceWizard/Attack/sprite.png);
  animation: enemy-attack 1s steps(1) 1;
}

@keyframes enemy-stand {
  from {
    background-position: -5px -5px;
  }
  10% {
    background-position: -527px -5px;
  }
  20% {
    background-position: -1049px -5px;
  }
  30% {
    background-position: -1571px -5px;
  }
  40% {
    background-position: -2093px -5px;
  }
  50% {
    background-position: -2615px -5px;
  }
  60% {
    background-position: -3137px -5px;
  }
  70% {
    background-position: -3659px -5px;
  }
  80% {
    background-position: -4181px -5px;
  }
  90% {
    background-position: -4703px -5px;
  }
  to {
    background-position: -4703px -5px;
  }
}

