.hero-container {
  width: 512px;
  height: 512px;
  overflow: hidden;
  display: flex;
  justify-content: center;
}

.hero {
  width: 512px;
  height: 512px;
  background-position: -5px -5px;
  background-repeat: no-repeat;
  display: block;
  margin: 0 auto;
  transition:.2s; 
}

.hero.stand {
  background-image: url(./img/Knight/Stand/sprite.png);
  animation: hero-stand 1s steps(1) infinite;
}

.hero.attack {
  background-image: url(./img/Knight/Attack/sprite.png);
  animation: hero-attack 1s steps(1);
}

.hero.hit {
  background-image: url(./img/Knight/Hit/sprite.png);
  animation: hero-hit 1s steps(1) 1;
}

.hero.die {
  background-image: url(./img/Knight/Die/sprite.png);
  animation: hero-die 1s steps(1) 1 forwards;
}

@keyframes hero-stand {
  from {
    background-position: -5px -5px;
  }
  10% {
    background-position: -527px -5px;
  }
  20% {
    background-position: -1049px -5px;
  }
  30% {
    background-position: -1571px -5px;
  }
  40% {
    background-position: -2093px -5px;
  }
  50% {
    background-position: -2615px -5px;
  }
  60% {
    background-position: -3137px -5px;
  }
  70% {
    background-position: -3659px -5px;
  }
  80% {
    background-position: -4181px -5px;
  }
  90% {
    background-position: -4703px -5px;
  }
  to {
    background-position: -4703px -5px;
  }
}

