.health-bar {
  color: white;
  font-size: 20px;
  transition: 0.5s;
  padding: 5px;
  margin: 5px;
  margin-bottom: 20px;
}

.health-bar__indicator {
  display: flex;
  justify-content: left;
  width: 100%;
  border: 1px solid grey;
  border-radius: 5px;
  background-color: rgb(233, 233, 233);
  overflow: hidden;
  color: black;
  transition: 0.5s;
}

.health-bar__indicator>div {
  background-color: rgb(94, 202, 52);
  width: 50%;
  height: 20px;
  border-radius: 5px;
}

.health-bar__indicator>div:hover {
  background-color: rgb(176, 240, 151);
  transition: 0.5s;
}