.control-panel {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  transition: 0.5s;
}

.control-panel button{
  margin: 30px;
  padding: 30px;
  border-radius: 50%;
  transition: 0.5s;
}

.control-panel button:hover {
  box-shadow: 0 0 10px rgba(0,0,0,0.5);
  transition: 0.5s;
  background-color: tomato;
  cursor: pointer;
}