.score-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
}

.score {
  border-collapse: collapse;
  margin: 20px;
  width: 50vw;
}

.score td, 
.score th {
  border-collapse: collapse;
  border: 1px solid grey;
  text-align: center;
}