.modal-dialog-container {
  display: flex;
  justify-content: center;
  width: 60vw;
  min-width: 300px;
  height: 50vh;
  min-height: 200px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%,-50%);
  background-color: chocolate;
  border: 1px solid grey;
  border-radius: 10px;
  z-index: 99;
}
.modal-dialog {
  align-self: center;
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: mediumorchid;
  border: 1px solid grey;
  border-radius: 10px;
  width: 55vw;
  height: 45vh;
}

.btn-close {
  position: absolute;
  left: 100%;
  transform: translate(-100%, 0%);
}
