.page-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
}
.battle-container {
  width: 100vw;
  height: 100vh;
  background-image: url(/rss-game/static/media/2.dce40399.jpg);
  background-repeat: no-repeat;
  background-position: center;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.battle-header {
  color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 5px;
  margin: 5px;
  background-color: rgba(255, 255, 255, 0.1);
  -webkit-transition: 0.5s;
  transition: 0.5s;
}

.battle-header:hover {
  box-shadow: 0 0 10px rgba(0,0,0,0.5);
  background-color: rgba(255, 255, 255, 0.3);
  -webkit-transition: 0.5s;
  transition: 0.5s;
}

.battle-nav {
  display: flex;
  justify-content: center;
  padding: 5px;
  margin: 5px;
}

.battle-nav button {
  margin: 10px;
  padding: 10px;
}

.battle-field {
  box-sizing: border-box;
  display: flex;
  justify-content: space-around;
}

.hero-container {
  width: 512px;
  height: 512px;
  overflow: hidden;
  display: flex;
  justify-content: center;
}

.hero {
  width: 512px;
  height: 512px;
  background-position: -5px -5px;
  background-repeat: no-repeat;
  display: block;
  margin: 0 auto;
  -webkit-transition:.2s;
  transition:.2s; 
}

.hero.stand {
  background-image: url(/rss-game/static/media/sprite.6781752a.png);
  -webkit-animation: hero-stand 1s steps(1) infinite;
          animation: hero-stand 1s steps(1) infinite;
}

.hero.attack {
  background-image: url(/rss-game/static/media/sprite.4405193b.png);
  -webkit-animation: hero-attack 1s steps(1);
          animation: hero-attack 1s steps(1);
}

.hero.hit {
  background-image: url(/rss-game/static/media/sprite.b95c4552.png);
  -webkit-animation: hero-hit 1s steps(1) 1;
          animation: hero-hit 1s steps(1) 1;
}

.hero.die {
  background-image: url(/rss-game/static/media/sprite.1e2bc7ac.png);
  -webkit-animation: hero-die 1s steps(1) 1 forwards;
          animation: hero-die 1s steps(1) 1 forwards;
}

@-webkit-keyframes hero-stand {
  from {
    background-position: -5px -5px;
  }
  10% {
    background-position: -527px -5px;
  }
  20% {
    background-position: -1049px -5px;
  }
  30% {
    background-position: -1571px -5px;
  }
  40% {
    background-position: -2093px -5px;
  }
  50% {
    background-position: -2615px -5px;
  }
  60% {
    background-position: -3137px -5px;
  }
  70% {
    background-position: -3659px -5px;
  }
  80% {
    background-position: -4181px -5px;
  }
  90% {
    background-position: -4703px -5px;
  }
  to {
    background-position: -4703px -5px;
  }
}

