.health-bar {
  color: white;
  font-size: 20px;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  padding: 5px;
  margin: 5px;
  margin-bottom: 20px;
}

.health-bar__indicator {
  display: flex;
  justify-content: left;
  width: 100%;
  border: 1px solid grey;
  border-radius: 5px;
  background-color: rgb(233, 233, 233);
  overflow: hidden;
  color: black;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}

.health-bar__indicator>div {
  background-color: rgb(94, 202, 52);
  width: 50%;
  height: 20px;
  border-radius: 5px;
}

.health-bar__indicator>div:hover {
  background-color: rgb(176, 240, 151);
  -webkit-transition: 0.5s;
  transition: 0.5s;
}
.player-container {
  display: flex;
  flex-direction: column;
}
.control-panel {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}

.control-panel button{
  margin: 30px;
  padding: 30px;
  border-radius: 50%;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}

.control-panel button:hover {
  box-shadow: 0 0 10px rgba(0,0,0,0.5);
  -webkit-transition: 0.5s;
  transition: 0.5s;
  background-color: tomato;
  cursor: pointer;
}
.modal-dialog-container {
  display: flex;
  justify-content: center;
  width: 60vw;
  min-width: 300px;
  height: 50vh;
  min-height: 200px;
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
  background-color: chocolate;
  border: 1px solid grey;
  border-radius: 10px;
  z-index: 99;
}
.modal-dialog {
  align-self: center;
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: mediumorchid;
  border: 1px solid grey;
  border-radius: 10px;
  width: 55vw;
  height: 45vh;
}

.btn-close {
  position: absolute;
  left: 100%;
  -webkit-transform: translate(-100%, 0%);
          transform: translate(-100%, 0%);
}

.task-list-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.task-list-container button {
  margin: 10px;
  padding: 10px;
}

